
#app {
  width: 100%;
  height: 100vh;
  max-width: 640px;
  min-width: 300px;
  margin: 0 auto;
  position: relative;
}
.nav-main {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: rgba(49, 49, 49, 0.97);
}
.nav-main>ul {
  width: 100%;
}
.nav-main>ul>li {
  width: 25%;
  float: left;
  text-align: center;
  padding: 5px 0 2px 0;
}
.nav-main>ul>li>a {
  color: #808080;
}
.nav-main>ul>li>a.router-link-active {
  color: #fff;
}
.nav-main>ul>li>a>p {
  margin: 0;
}
.movein-enter-active,
.movein-leave-active {
  -webkit-transition: all .5s;
  transition: all .5s;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.movein-enter,
.movein-leave-active {
  opacity: 0;
  -webkit-transform: translateX(100vw);
          transform: translateX(100vw);
}



